import React, { useEffect, useState } from "react"
import { Grid } from "@mui/material"
import Tabs from "./Tabs"
import "../ProductDesign.scss"
import Heading from "../../shared/Heading"
function DesignTabs() {
  const [innerHeight, setInnerHeight] = useState(429)
  const [heights, setHeights] = useState({
    designTabsHeight: 400,
    screenHeight: 200,
  })

  useEffect(() => {
    const handleResize = () => {
      setInnerHeight(window.innerHeight)
    }
    window.addEventListener("resize", handleResize, { passive: true })
    return () => window.removeEventListener("resize", handleResize)
  }, [])

  useEffect(() => {
    const element = document.querySelector(".design-tabs")
    const designTabsHeight = element.clientHeight
    const screenHeight = window.innerHeight
    setHeights({
      designTabsHeight,
      screenHeight,
    })
  }, [innerHeight])

  console.log(
    "top-heitht-in element",
    heights.screenHeight - heights.designTabsHeight + 96
  )

  return (
    <div className="design-parent-sticky">
      <div
        className="desing-section-sticky"
        style={{
          top: `${heights.screenHeight - heights.designTabsHeight + 96}px`,
        }}
      >
        <div className="design-tabs">
          <div className="container background-img">
            <Grid container pt={{ md: 15, sm: 12, xs: 7 }}>
              <Grid item md={5}>
                <div
                // data-aos="fade-up"
                // data-aos-anchor-placement="center-bottom"
                // data-aos-duration="1500"
                >
                  <Heading variant="40" color="white">
                    Here’s How We Design Your Vision{" "}
                  </Heading>
                </div>
              </Grid>

              <Grid item md={7}></Grid>
              <Grid item md={5}>
                <div
                  // data-aos="fade-up"
                  // data-aos-anchor-placement="center-bottom"
                  // data-aos-duration="1500"
                  className="text"
                >
                  Through inventive and intuitive design, we allow your brand to
                  stand out from the crowd by leveraging your goals to craft a
                  creative vision.{" "}
                </div>
              </Grid>
            </Grid>
            <Tabs />
          </div>
        </div>
      </div>
    </div>
  )
}

export default DesignTabs
